import React, { useState } from "react";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import { TestData } from "./TestData";
import { IconButton, makeStyles, Grid, Typography } from "@material-ui/core";

const useStyles = makeStyles({
  slider: {
    position: "relative",
    height: "100vh",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },

  image: {
    width: "600px",
    height: "400px",
    paddingLeft:'10px'
  },

  rightarrow: {
    position: "absolute",
    top: "50%",
    right: "32px",
    fontSize: "3rem",
    color: "#000",
    zIndex: 10,
  },

  leftarrow: {
    position: "absolute",
    top: "50%",
    left: "32px",
    fontSize: "3rem",
    color: "#000",
    zIndex: 10,
  },

  slide: {
    opacity: 0,
    transitionDuration: "1s ease",
  },
  test: {
    padding:'25px',
    opacity: 0,
    transitionDuration: "1s ease",
  },
  slideactive: {
    opacity: 1,
    transitionDuration: "1s",
    transform: "scale(1.08)",
  },

  sections: {
    display: "grid",
    gridTemplateColumns: "1fr 1fr",
    width: "90vw",
   padding:'25px',
    alignItems: "center",
    backgroundColor: "white",
    borderRadius:'5px'
  },
});

const Test = ({ tests }) => {
  const classes = useStyles();
  const [current, setCurrent] = useState(0);
  const length = tests.length;

  const prevTest = () => {
    setCurrent(current === length - 1 ? 0 : current + 1);
  };

  const nextslide = () => {
    setCurrent(current === 0 ? length - 1 : current - 1);
  };

  if (!Array.isArray(tests) || tests.length <= 0) {
    return null;
  }

  return (
    <div className={classes.slider}>
      <IconButton onClick={prevTest} className={classes.leftarrow}>
        <ArrowBackIosIcon />
      </IconButton>

      <IconButton onClick={nextslide} className={classes.rightarrow}>
        <ArrowForwardIosIcon />
      </IconButton>
      <Grid className={classes.sections}>
        <Grid>
          {TestData.map((test, index) => {
            return (
              <div
                className={
                  index === current ? classes.slideactive : classes.slide
                }
                key={index}
              >
                {index === current && (
                  <img
                    src={test.image}
                    alt="travel"
                    className={classes.image}
                  />
                )}
              </div>
            );
          })}
        </Grid>
        <Grid>
          {TestData.map((test, index) => {
            return (
              <div
                className={
                  index === current ? classes.slideactive : classes.text
                }
                key={index}
              >
                {index === current && (
                  <Typography>{test.description}</Typography>
                )}
              </div>
            );
          })}
        </Grid>
      </Grid>
    </div>
  );
};

export default Test;
